import React from "react";

import {
  MDBCarousel,
  MDBCarouselInner,
  MDBCarouselItem,
  MDBView,
  MDBContainer
} from "mdbreact";

import FittedImage from "react-fitted-image";


import suvin_pic_1 from "./Home_Image/suvin01.png"
import suvin_pic_2 from "./Home_Image/suvin02.png"

import suvin_pic_1_mobile from "./Home_Image/suvin01_mobile.png"
import suvin_pic_2_mobile from "./Home_Image/suvin02_mobile.png"


/*
  px-0 : x 
  activeItem={1} : 1  
  length={2} :    2
  showControls={true} :    
  showIndicators={true} :   
  z-depth-1 : 
        import   .

  <MDBContainer> fluid  className="w-100"    

  home__carousel     


  fittedimage   div  background  
*/

function HomeCarousel() {
  return (
    <MDBContainer fluid className="px-0">
    <MDBCarousel
      activeItem={1}
      length={2}
      showControls={true}
      showIndicators={true}
      className="z-depth-0"
    >
      <MDBCarouselInner>
        <MDBCarouselItem itemId="1">

          <MDBView>
                <FittedImage fit="cover" src={suvin_pic_1} className="carousel__css first_carousel" />
                
          </MDBView>

        </MDBCarouselItem>
        <MDBCarouselItem itemId="2">

          <MDBView>
              <FittedImage fit="cover" src={suvin_pic_2} className="carousel__css second_carousel" />
          </MDBView>

        </MDBCarouselItem>

      </MDBCarouselInner>
    </MDBCarousel>
  </MDBContainer>
  );
}


export default HomeCarousel;