import React, { Component } from "react";
import {
MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavItem, MDBNavLink, MDBNavbarToggler, MDBCollapse
} from "mdbreact";

import { HashRouter, useLocation } from "react-router-dom";

import FittedImage from "react-fitted-image";
import header_sub_bg from "./header_sub_bg.png";


/*
    isOpen         
    mx-auto   x        
     maxWidth 1100     ,
          z-depth-0 ,
    , BrowserRouter  HashRouter    ,
     <a href>   .
            (   App.js  )

    FittedImage      , props   home   
*/

class Header extends Component {
state = {
  isOpen: false
};

toggleCollapse = () => {
  this.setState({ isOpen: !this.state.isOpen });
}

render() {
  return (
      <div>

            <div style={{  backgroundColor: "#9e4f89" }}>
                <MDBNavbar color="" dark expand="md" className="z-depth-0 mx-auto"  style={{maxWidth: 1100}}>
                    <MDBNavbarBrand>
                        <MDBNavLink to="/"><strong className="white-text">Suvin Cosmetic</strong></MDBNavLink>
                    </MDBNavbarBrand>

                    <MDBNavbarToggler onClick={this.toggleCollapse} />
                    <MDBCollapse isOpen={this.state.isOpen} navbar>


                        <MDBNavbarNav>

                            <MDBNavItem>
                                <MDBNavLink to="Company">Information</MDBNavLink>
                            </MDBNavItem>

                            <MDBNavItem>
                                <MDBNavLink to="Competence">Business</MDBNavLink>
                            </MDBNavItem>

                            <MDBNavItem>
                                <MDBNavLink to="Product">Product</MDBNavLink>
                            </MDBNavItem>

                            <MDBNavItem>
                                <MDBNavLink to="Global_Network">Global Network</MDBNavLink>
                            </MDBNavItem>

                            <MDBNavItem>
                                <MDBNavLink to="Inquiry">Inquiry</MDBNavLink>
                            </MDBNavItem>

                            <MDBNavItem>
                                <MDBNavLink to="Customer">Customer</MDBNavLink>
                            </MDBNavItem>

                        </MDBNavbarNav>

                    </MDBCollapse>
                </MDBNavbar>
            </div>

        <DisplayBG />

    </div>

    );
  }
}

export default Header;


//     
function DisplayBG() {
    let location = useLocation();
    let display;

    if ( location.pathname === "/" ){
        display = "none"
    } else {
        display = "block"
    }

    //css       backgroundImage:`url(${Motto})`
    return (
        <FittedImage fit="cover" src={header_sub_bg} style={{height: "169px", display}} className="headerBG__css" />
    );

}